import React from 'react';
import {Button, Intent, Popover, PopoverInteractionKind, Position} from '@blueprintjs/core';
import {withRouter} from 'react-router-dom';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {editService, deleteService, loadServices} from '../../../../actions/MachineServicesActions';
import _isEqual from 'lodash/isEqual';
import {DELETE, EDIT} from "../../../../api/constants/Actions";
import _find from 'lodash/find';
import {SERVICE} from "../../../../api/constants/Entities";
import {isFormValid} from "../../../../helpers";
import {DELETE_MACHINE_SERVICE, EDIT_MACHINE_SERVICE} from "../../../../helpers/constants";
import Toaster from "../../../Common/Toaster";
import Can from "../../../../helpers/Can";
import { SETTINGS } from '../../../../api/constants/Pages';

class View extends React.Component{

    state = {
        service: {},
        serviceBuffer: {},
        serviceRaw: {},
        confirmDelete: false
    };

    static getDerivedStateFromProps(nextProps, prevState){
        const {serviceRaw: oldServiceRaw} = prevState;
        const {services, match: {params: {id}}} = nextProps;
        let serviceRaw = _find(services, {id: Number.parseInt(id, 10)}) || {};
        const service = {...serviceRaw};

        if(_isEqual(oldServiceRaw, serviceRaw))
            return null;

        return {service, serviceBuffer: {...service}, serviceRaw};
    }

    componentDidUpdate(prevProps){
        const {OPERATION_SUCCESSFUL: OLD_OPERATION_SUCCESSFUL, OPERATION_FAILED: OLD_OPERATION_FAILED} = prevProps;
        const {OPERATION_SUCCESSFUL, OPERATION_FAILED, history, authUser: {token}, loadServices} = this.props;

        if(!_isEqual(OLD_OPERATION_SUCCESSFUL, OPERATION_SUCCESSFUL)){
            loadServices(token, {});
            const service = this.state.service.name.toUpperCase();

            if(OPERATION_SUCCESSFUL.action === EDIT_MACHINE_SERVICE){
                history.goBack();
                Toaster.show({
                    message: `${service} Successfully Updated 😃`,
                    intent: Intent.SUCCESS,
                    icon: 'tick' });
            }

            if(OPERATION_SUCCESSFUL.action === DELETE_MACHINE_SERVICE){
                history.goBack();
                Toaster.show({
                    message: `${service} Successfully Deleted 😃`,
                    intent: Intent.SUCCESS,
                    icon: 'tick' });
            }
        }

        if(!_isEqual(OLD_OPERATION_FAILED, OPERATION_FAILED)){
            const service = this.state.service.name.toUpperCase();
            if(OPERATION_FAILED.action === EDIT_MACHINE_SERVICE){
                console.log(OPERATION_FAILED.data);
                Toaster.show({
                    message: `Could Not Update ${service} 😞`,
                    intent: Intent.DANGER,
                    icon: 'error'
                });
            }
            if(OPERATION_FAILED.action === DELETE_MACHINE_SERVICE){
                console.log(OPERATION_FAILED.data);
                Toaster.show({
                    message: `Could Not Delete ${service} 😞`,
                    intent: Intent.DANGER,
                    icon: 'error'
                });
            }
        }
    }

    _handleInputChange = (name, value) => {
        this.setState(() => {
            return {
                service: {...this.state.service, [name]: value}
            }
        });
    }

    _handleDelete = () => {
        this.props.deleteService(this.props.authUser.token, this.state.service.id);
        this.setState({confirmDelete: false});
    }

    _handleEdit = () => {
        const {authUser: {token}, editService} = this.props;
        const {service: {name, display, path}, serviceRaw: {id}} = this.state;

        let data = new FormData();

        data.append('name', name);
        data.append('display', display);
        data.append('path', path);

        editService(token, id, data);
    }

    _isFormValid = () => {
        const {service: {name, display, path}} = this.state;

        let required = {name, display, path};

        return isFormValid(required);
    }

    render(){
        const {history, editingService, deletingService, authUser} = this.props;
        const {service: {name, display, path}, serviceBuffer, service, confirmDelete, serviceRaw} = this.state;
        const disableSaveButton = !this._isFormValid() || _isEqual(service, serviceBuffer);
        const serviceNotFound = !serviceRaw.id;

        const editActionAllowed = authUser.root ? true : Can.User(authUser).perform(EDIT, SERVICE, SETTINGS);
        const deleteActionAllowed = authUser.root ? true : Can.User(authUser).perform(DELETE, SERVICE, SETTINGS);

        return [
            <div className="bp3-dialog-body">

                <label className="bp3-label">
                    Name
                    <input
                        value={name || ''}
                        name={'name'} onChange={({target: {name, value}}) => this._handleInputChange(name, value)}
                        className="bp3-input bp3-fill" type="text" dir="auto" />
                </label>
                <label className="bp3-label">
                    Display Name
                    <input
                        value={display || ''}
                        name={'display'} onChange={({target: {name, value}}) => this._handleInputChange(name, value)}
                        className="bp3-input bp3-fill" type="text" dir="auto" />
                </label>
                <label className="bp3-label">
                    Full Path
                    <input
                        value={path || ''}
                        name={'path'} onChange={({target: {name, value}}) => this._handleInputChange(name, value)}
                        className="bp3-input bp3-fill" type="text" dir="auto" />
                </label>
            </div>,
            <div className="bp3-dialog-footer">
                <div className="bp3-dialog-footer-actions">
                    {serviceNotFound && (
                        <Button
                            intent={Intent.PRIMARY}
                            onClick={() => history.goBack()}
                            icon="undo"
                            text="Go Back"
                        />
                    )}

                    <Popover isOpen={this.state.confirmDelete} interactionKind={PopoverInteractionKind.CLICK} position={Position.BOTTOM}>
                        <Button
                            disabled={deletingService || editingService || serviceNotFound || !deleteActionAllowed}
                            loading={deletingService}
                            onClick={() => this.setState({confirmDelete: true})}
                            intent={Intent.DANGER}
                            text={'delete'}
                            icon="trash" />
                        <div style={{ padding: ".5rem", display: "flex", flexDirection: "column" }}>
                            <header>Are you sure?</header>
                            <div style={{ display: "flex", justifyContent: "space-evenly", marginTop: ".5rem" }}>
                                <Button onClick={this._handleDelete} small text="yes" intent={Intent.SUCCESS}/>
                                <Button onClick={() => this.setState({confirmDelete: false})} small text="no" intent={Intent.DANGER}/>
                            </div>
                        </div>
                    </Popover>

                    <Button
                        disabled={disableSaveButton || deletingService || serviceNotFound || !editActionAllowed}
                        loading={editingService}
                        intent={Intent.PRIMARY}
                        onClick={this._handleEdit}
                        icon="tick"
                        text="save"
                    />
                </div>
            </div>
        ];
    }

    static propTypes = {
        history: PropTypes.object.isRequired,
        authUser: PropTypes.object.isRequired,
        match: PropTypes.object.isRequired,
        OPERATION_FAILED: PropTypes.object.isRequired,
        OPERATION_SUCCESSFUL: PropTypes.object.isRequired,
        editService: PropTypes.func.isRequired,
        deleteService: PropTypes.func.isRequired,
        editingService: PropTypes.bool.isRequired,
        deletingService: PropTypes.bool.isRequired,
        services: PropTypes.array.isRequired,
        loadServices: PropTypes.func.isRequired,
    };
}

const mapStateToProps = (
    {authUser, OPERATION_FAILED, OPERATION_SUCCESSFUL, editingService, deletingService, services}) => (
        {authUser, OPERATION_FAILED, OPERATION_SUCCESSFUL, editingService, deletingService, services});
const mapDispatchToProps = dispatch => bindActionCreators({deleteService, editService, loadServices}, dispatch);

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(View));


